export class DelayBuffer {
    buffer;
    index = 0;
    constructor(size) {
        this.buffer = new Float32Array(size);
    }
    pushPop(sample) {
        const delayedSample = this.buffer[this.index];
        this.buffer[this.index] = sample;
        this.index = (this.index + 1) % this.buffer.length;
        return delayedSample;
    }
}
